
html,body{
  font-size: 14px;
  font-family: Microsoft YaHei !important;
  width: 100%;
  min-width: 600px;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: #F1F3F7;
}
body,h1,h2,h3,h4,h5,h6,p,ul,ol,li,dl,dt,dd,a,textarea,input,button,span,em,strong,img,div {
  -webkit-touch-callout: none;
  -moz-touch-callout: none;
  -ms-touch-callout: none;
  touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  -moz-tap-highlight-color: transparent;
  -ms-tap-highlight-color: transparent;
  tap-highlight-color: transparent;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  text-decoration: none;
  font-family: Microsoft YaHei !important;
}
#app, .Am-home{
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.Am-main{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
}
/* 公共右侧样式 */
.Am-group{
  border-radius: 4px;
  margin: 10px;
  /*padding: 5px;*/
  background: #FFFFFF;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  overflow: hidden;
}
/* 隐藏Element横向滚动条 */
.Am-group .el-scrollbar__wrap{ /*右侧处理*/
  overflow-x: hidden;
}
.slider .el-scrollbar__wrap{ /*左侧处理*/
  overflow-x: hidden;
}

